import { State, TransformsPlugin, RenderPlugin, minimalLight } from 'shallot';
import { OrbitPlugin } from 'shallot/extras';
import { GridArrangementPlugin, setupGridUI } from '../src';
import { setupCanvas, createVisibilityLoop } from '@blog-components/iframe';

async function init(): Promise<void> {
  const canvas = document.getElementById('canvas') as HTMLCanvasElement;
  if (!canvas) return;

  setupCanvas(canvas);
  window.addEventListener('resize', () => setupCanvas(canvas));

  const state = await State.new()
    .withCanvas(canvas)
    .withLoading(minimalLight(canvas))
    .withPlugins(TransformsPlugin, RenderPlugin, OrbitPlugin, GridArrangementPlugin)
    .withScene('/scenes/grid.scene')
    .build();

  setupGridUI(state);

  createVisibilityLoop(state);
}

if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', init);
} else {
  init();
}
